import * as React from 'react';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';


export default function VariantButtonGroup({terminal,ledsettings}) {


  const modes = ledsettings.Modes;

  const listItems = modes.map(product =>
    <Grid item xs={6}>
   
    <Button  variant="contained"  sx={{fontSize: 12,  width: 1}}
      onClick={()=>{ terminal.send("M"+product.id);}}
    >
      {product.name}
    </Button>
   
    </Grid>
  );


  return (
    <Box justifyContent="center">
      <Grid sx={{padding:5}} container rowSpacing={1} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
        {listItems}
      </Grid>
    </Box>



  );
}