import Reac, {useEffect, useState,useRef  } from 'react';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import { HexColorPicker } from "react-colorful";


export default function ColorPickerPage({terminal,ledsettings}) {

    const reciveHandler = (data)=>{
        let options = data.detail.split(":");
        let leds = [];

        if(options[0] == 'Q' && options[1] != "NOTSET")
        {
            let allLeds = options[1].substring(1).split('#'); // remove first hash so we dont get a blank start
            if(allLeds.length == ledsettings.NumLEDS)
            {
                allLeds.forEach((ledCol)=>{
                    leds.push("#"+ledCol);
            
                });
            }
            else
            {
                leds = ledarray();
            }
        }
        else
        {
            leds = ledarray();
        }
        setColorArray(leds);

    };



    const ledarray = ()=>
    {
        let leds = [];
        if(ledsettings.NumLEDS > 0)
        {
            for(let i =0; i < ledsettings.NumLEDS; i++)
            {
                leds[i]="#ffffff";
            }
        }
        return leds;
    };



    const initialized = useRef(false)



    useEffect(()=>
    {
        window.addEventListener("BTDataRecived", reciveHandler);
        if(!initialized.current)
        {
            terminal.send("Q");
            initialized.current = true;
        }
        return () => {
            window.removeEventListener('BTDataRecived',reciveHandler);
          };
    },[]);
     

    const [color, setColor] = useState("#b32aa9");
    const [colorarray, setColorArray] = useState([]);

    const [allLedColour, setAllLedColour] = useState("#b32aa9");

    const [sending,setSending] = useState(false);

    const [lastUpdate,setLastUpdate] = useState([]);



    const updateAllColours = (val) => {
        setColor(val);
        setAllColourFunc(val); 
    };

    
    const setAllColourFunc = (cc) =>
    {

        if(colorarray.length < 1)
        {
            return;

        }

        let arr = [];
        for(let i =0; i < ledsettings.NumLEDS; i++)
        {
            arr[i]=cc;

        }
        setColorArray(arr);
    } 


    useEffect(()=>
    {
        if(colorarray.length < 1)
        {
            return;
        }

        if(colorarray.join() != lastUpdate.join())
        {
            sendLedUpdate(colorarray);
        }
    },[colorarray,sending]);
    

    function sendLedUpdate(array)
    {
        if(sending)
        {
            return;
        }

        setSending(true);

        let ledstring = "";
        for(let i =0; i < ledsettings.NumLEDS; i++)
        {  

            ledstring += array[i];
        }

        if(ledstring.substring(1).split('#').length == ledsettings.NumLEDS)
        {
            terminal.send("C" + ledstring).then(()=>{
                setSending(false);
                setLastUpdate([...array]);
            });
        }
    }


    function handleUpdateSingleLED(index, value)
    {
        if(colorarray.length < 1)
        {
            return;
        }

        let array = [...colorarray];

        array[index]=value;

        setColorArray(array);
    }

    const allLEDS = () => 
    {
        let content1 = [];

        if(ledsettings.NumLEDS > 0)
        {

            for(let i = 0; i<ledsettings.NumLEDS; i++ )
            {

                content1.push( 
                <div style={{padding:10}}>
                <HexColorPicker color={colorarray[i]} onChange={e=>{handleUpdateSingleLED(i,e)}} />
          
                    <div className="value" style={{ borderLeftColor: color }}>
                    Current color is {colorarray[i]}
                  </div>
                  </div>
              );

            }

        }
        return content1;

    };

  return (
    <section className="resposive example">
    <section>Number of LEDS: {ledsettings.NumLEDS}</section>

    <HexColorPicker color={color} onChange={e=> {updateAllColours(e); }} />

    <div className="value" style={{ borderLeftColor: color }}>
      Current color is {color}
    </div>

    <div className="pickAColourSingle" style={{display:"flex", flexWrap:"wrap", alignItems:"center", justifyContent:"center", paddingBottom:50}}>

    {allLEDS()}



    </div>

    </section>



  );
}