import Reac, { useState, useEffect, useRef } from 'react';
import { Routes, Route } from "react-router-dom"

import logo from './logo.svg';
import './App.css';
import BluetoothTerminal from './Helpers/BluetoothTerminal';
import VariantButtonGroup from './ModeSelectButtons';
import { Box, Button } from '@mui/material';
import BluetoothConnectedIcon from '@mui/icons-material/BluetoothConnected';
import ResponsiveAppBar from './ResponsiveAppBar';
import SimpleBottomNavigation from './BottomNavBar';
import ColorPickerPage from './ColourPickerPage';
import AnimationMakerPage from './AnimationMakerPage';
import ReconnectingModal from './ReconnectingComponant' 
import ConnectionFailedComponant from './ConnectionFailedComponant';

const ledSettings = {NumLEDS:-1,Modes:[], NumUserAnimations:0};
//var serviveUuid = "6e400001-b5a3-f393-e0a9-e50e24dcca9e";
//var characteristicUuid  = "6e400003-b5a3-f393-e0a9-e50e24dcca9e";

var serviveUuid = "000b8ae9-c117-4ee3-97e0-29a37d4b58a3";
var characteristicUuid  = "4d4bb4ab-b095-4e27-a3de-6f43973a1de3";


const terminal = new BluetoothTerminal(serviveUuid,characteristicUuid,'\n','\n');

// Create the event.

const bob = {reciveHandler:(data)=> {}};



 terminal.receive = function(data) {
  let option = data.charAt(0);

  if(option == "S")
  {
      let options = data.split(":");

      ledSettings.NumLEDS = parseInt(options[1]);
      ledSettings.Brightness = parseInt(options[2]);
      ledSettings.CurrentAnimationMode = parseInt(options[3]);
      ledSettings.Modes = [];
      let id = 0;
      options[4].split(',').forEach((mode) =>{
        ledSettings.Modes.push({id:id,name:mode});
        id++;
       });
      ledSettings.NumUserAnimations = parseInt(options[5])
      const customEvent = new Event("SettingsUpdated", {"bubbles":true, "cancelable":false});
      window.dispatchEvent(customEvent);
      console.log(ledSettings);
  }
  else
  {
    bob.reciveHandler(data);
    const customEvent = new CustomEvent("BTDataRecived", {"bubbles":true, "cancelable":false,"detail":data});
    window.dispatchEvent(customEvent);
  }
};


function App() {
  const connectionFailRef = useRef();
  connectionFailRef.current = function() {};
  const [reconnecting,setReconnecting] = useState(false);

  const [testText,setTestText] = useState("Hello");

  terminal.tryingReconnect = function() {
    setReconnecting(true);
  
  }
  terminal.reconnectFailed = function() {
  
    setReconnecting(false);
    connectionFailRef.current.toggleShow();
  }

  const [espsettings, setEspSettings] = useState(ledSettings);
  
  const handleSettingsUpdated = (event) => {
    setEspSettings({...ledSettings});

  };

  useEffect(() => {
    window.addEventListener("SettingsUpdated", handleSettingsUpdated);

    return () => {
      window.removeEventListener('SettingsUpdated',handleSettingsUpdated);
    };
  }, []);


  return (
    <div className="App">

      <ResponsiveAppBar setts={espsettings} terminal={terminal} />
      <ReconnectingModal testText={testText} showModel={reconnecting} />
      <ConnectionFailedComponant toggler={connectionFailRef}  />



      <Box sx={{paddingTop:2, paddingBottom:10}}>

        <Routes>
          <Route path="/" element={<VariantButtonGroup ledsettings={espsettings} terminal={terminal} /> } />
          <Route path="/colorpicker" element={<ColorPickerPage ledsettings={espsettings} terminal={terminal}  /> } />
          <Route path="/animation-maker" element={<AnimationMakerPage ledsettings={espsettings} terminal={terminal} /> } />
        </Routes>
      </Box>

      <SimpleBottomNavigation />
 
    </div>
  
  );
}

export default App;
