import * as React from 'react';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import AnimationFrameSingle  from './AnimationFrameSingle';


function CustomTabPanel(props) {
    const { children, value, index, ...other } = props;
  
    return (
      <div
        role="tabpanel"
        hidden={value !== index}
        id={`simple-tabpanel-${index}`}
        aria-labelledby={`simple-tab-${index}`}
        {...other}
      >
        {value === index && (
          <Box sx={{ p: 3 }}>
            {children}
          </Box>
        )}
      </div>
    );
  }
  
  CustomTabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
  };


const frameDataTemplate = {ledData:[],tranSpeed:0,transitionID:"0"}


export default function AnimationManagerTab({terminal,ledsettings,eventManager,animationID}) {
    const [value, setValue] = React.useState(0);
    const [tabData, setTabData] = React.useState([{...frameDataTemplate},{...frameDataTemplate},{...frameDataTemplate}]);


    eventManager.SaveEvent = async function()
    {
        let i = 0;
        const dlength = tabData.length;

        for await (const frame of tabData)
        {
          const sendSTring = "A" + animationID + ":" +  i + ":" + dlength + ":" + frame.transitionID + ":" + frame.tranSpeed +  ":" + frame.ledData.join("");
          await terminal.send(sendSTring );
          i++;
        }
    };


    const handleChange = (event, newValue) => {
      setValue(newValue);
    };

    function handleAddNewClick(tabID)
    {
        let sets = [...tabData];   
        sets.splice(tabID+1, 0, {ledData:[]});

        setTabData(sets);
        setValue(tabID+1);
    }
    
    function deleteClickHandler(tabID)
    {
        let sets = [...tabData];   
        sets.splice(tabID, 1);

        setTabData(sets);
    }

    const getTabHeaders = (data) =>
    {
        let content = [];
        let i = 0;
        data.forEach(element => {
            content.push(<Tab label={"Frame " + i} />);
            i++;
        })
        return (content)
    };

    function frameChangeEvent(eventData)
    {

        let sets = [...tabData];   
        sets[eventData.tab].ledData=eventData.ledData;
        sets[eventData.tab].tranSpeed=eventData.tranSpeed;
        sets[eventData.tab].transitionID=eventData.transitionID;
        setTabData(sets);
        localStorage.setItem("ANI_"+eventData.transitionID + "_" + eventData.tab,JSON.stringify({saved:false,data:sets[eventData.tab]}));

    }
    
    const getTabPages = (data) =>
    {

        let content = [];
        let i = 0;
        data.forEach(element => {
        
            content.push(
                <CustomTabPanel value={value} index={i}> 
                    <AnimationFrameSingle tabData={tabData[i]} tabNumber={i} frameChangeEvent={frameChangeEvent}  addClickHandler={handleAddNewClick} deleteClickHandler={deleteClickHandler}/> 
                </CustomTabPanel>
            
            );
           i++;
        })
        return (content)


    };

    return (
        <Box>
        <Box  sx={{ borderBottom: 1, borderColor: 'divider' }}  display="flex" justifyContent="center" width="100%">
          <Tabs
            className='frameSelectionTabs'
            scrollButtons="auto"
            variant="scrollable"
            value={value}
            onChange={handleChange}
            aria-label="Vertical tabs example"
            allowScrollButtonsMobile
          >
            {getTabHeaders(tabData)}
            </Tabs>
            </Box>
            <Box>
               {getTabPages(tabData)}
             </Box>       
        </Box>
            )



};